 tela sobre un soporte móvil, que se desliza a lo largo del extendido, el cual, durante su recorrido, se encarga de ir dejando el tejido posado sobre la mesa de corte. Con este sistema, se consiguen dos objetivos fundamentales: rapidez en los operarios para efectuar la operación y mayor aprovechamiento del tejido. La siguiente figura ilustra este tipo de extendido:")]),_vm._m(9),_vm._m(10),_vm._m(11),_c('p',{staticClass:"mb-4"},[_vm._v("A continuación, se observan los sistemas de extendido más comunes en la industria.")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-8 mb-5"},[_c('LineaTiempoD',{staticClass:"color-secundario"},[_c('div',{staticClass:"row",attrs:{"numero":"1","titulo":"Extendido cara arriba"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_vm._v("El extendido comienza en el extremo inicial de la marcada, donde se coloca el rollo, se deposita el tejido sobre la mesa hasta llegar al extremo final, donde se efectúa el corte a lo ancho del tejido. Luego se devuelve al vacío hasta el inicio del tejido y se repite la operación cuantas veces sea necesario. Es el extendido típico del tejido estampado de cuadros o de rayas. Estas capas de tela se extienden según el largo del trazo. Es importante determinar cuál es el derecho de la tela, ya que este debe quedar mirando hacia arriba con respecto a la mesa.")]),_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"485px"},attrs:{"src":require("@/assets/curso/tema7/img8.svg"),"alt":"imagen ambiental"}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"2","titulo":"Extendido cara a cara o Zigzag"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_vm._v("Se inicia en cualquiera de los extremos de la marcada. El tejido se deposita en el extremo elegido y el operario (máquina) se desliza hacia el otro extremo, dejando el tejido al mismo tiempo. Al llegar al extremo opuesto, éste es doblado y el operario (carro extendedor)