apa. Las siguientes capas deben quedar en pares derecho con derecho de la tela.")]),_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"485px"},attrs:{"src":require("@/assets/curso/tema7/img10.svg"),"alt":"imagen ambiental"}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"Trazar sobre tejido a rayas"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_vm._v("Es una variable entre los extendidos cara arriba y cara a cara girando, donde, si bien el círculo de trabajo es idéntico, las longitudes de las capas son diferentes. Esto, debido a que se intentó extender dentro de la misma marcada tallas con cantidades diferentes.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Este tipo de extendido es empleado por confeccionistas con gran variedad de modelos y pocas cantidades por tallas, puesto que de esta manera se puede completar un pedido con una sola marcada, ahorrándose todos los tiempos de preparación de marcada y cortes que se necesitarían si se extendieran todas las tallas individuales. Al mismo tiempo, se consigue mayor velocidad en el extendido, trazo y corte, y por lo tanto, un incremento en el rendimiento de cada mesa de corte. Es un extendido cara arriba en el que los largos de las capas de tela no son iguales. Las capas más largas se colocan debajo y las más cortas se colocan arriba, formando un escalón, este tipo de extendido se emplea para ajustar en un solo trazo diferentes cantidades en relación con tallas y colores de prendas a cortar.")]),_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"485px"},attrs:{"src":require("@/assets/curso/tema7/img11.svg"),"alt":"imagen ambiental"}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"5","titulo":"Extendido en forma tubular"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-0"},[_vm._v("El método de trabajo es idéntico a los extendidos cara arriba y Zigzag, las diferencias con respecto a ellos radican en la forma de c