rios sujetan el extendido y tiran de él para desplazarlo a un lugar ya determinado. Puede presentar deformaciones internas en el tendido debido a que la fuerza que se le imprime es diferente; para evitar esto, se coloca una hoja de papel kraft, con el objetivo de que el colchón se deslice fácilmente sobre la mesa.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Es importante tener en cuenta que la cara lisa del papel quede en contacto con la mesa y la cara áspera quede en contacto con las telas.")])])])]),_c('div',{staticClass:"row justify-content-center mb-lg-0",attrs:{"titulo":"Transporte por aerosuspensión"}},[_c('div',{staticClass:"col-lg-12 mt-3 mb-5"},[_c('div',{staticClass:"p-5 mb-4",staticStyle:{"background-color":"#FFF5FA"}},[_c('h4',{staticClass:"mb-4"},[_vm._v("Transporte por aerosuspensión")]),_c('p',{staticClass:"mb-0"},[_vm._v("Surgen los dispositivos de aire soplado acoplados a las mesas de corte. En síntesis, consiste en hacer fluir aire a cierta velocidad por unos orificios efectuados en el tablero de la mesa de corte. Esto hace que el bloque o colchón de tela se deslice fácilmente sobre la mesa, evitando así las deformaciones internas en el tendido, y por lo tanto, el operario hará un menor esfuerzo.")])])])]),_c('div',{staticClass:"row justify-content-center mb-lg-0",attrs:{"titulo":"Transporte por aerosuspensión y rodillos de avances"}},[_c('div',{staticClass:"col-lg-12 mt-3 mb-5"},[_c('div',{staticClass:"p-5 mb-4",staticStyle:{"background-color":"#FFF5FA"}},[_c('h4',{staticClass:"mb-4"},[_vm._v("Transporte por aerosuspensión y rodillos de avances")]),_c('p',{staticClass:"mb-0"},[_vm._v("Es el mismo sistema de aerosuspensión, pero se le acondicionan unos rodillos que giran constantemente. Esos rodillos están colocados longitudinalmente a la mesa, su objetivo es desplazar el tendido o colchón automáticamente. Hay que cuidar que uno de los extremos del tendido esté en contacto con estos rodillos. Un solo operario es necesario para mover el colchón.")])])]